@import url("https://fonts.googleapis.com/css?family=Verdana|Roboto|Share Tech Mono");

body,
html {
  height: 100%;
}

* {
  border: 0px solid red;
}

p {
  margin: 0px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
body {
  margin: 0 150px;
  display: flex;
  flex-wrap: wrap;
}

#header,
#footer {
  height: 50px;
  width: 100%;
  background-color: cadetblue;
  padding: 30px;
}
#left-column {
  width: 250px;
  min-height: calc(100% - 2 * 110px);
  margin-left: 5%;
  border-left: 2px solid #ffb739;
  border-right: 2px solid #ffb739;
  display: flex;
  justify-content: center;
  align-items: center;
}

#right-column {
  flex: 1;
  min-height: calc(100% - 2 * 110px);
  margin-right: 5%;
  border-right: 2px solid #ffb739;
  background-color: rgba(219, 232, 249, 0.2);
}

/* HEADER */

#logo {
  height: 60px;
  position: absolute;
  left: 170px;
  top: 25px;
}

.horizontal-nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.horizontal-nav li {
  display: inline-block;
  min-width: 5em;
  margin: 0 2em;
}
.horizontal-nav a {
  text-decoration: none;
  display: block;
  font-family: Arial;
  font-size: 18px;
  position: relative;
  color: #fff;
  padding: 0.5em 0;
}
.horizontal-nav a:hover {
  color: #000;
}

.horizontal-nav a:hover:before {
  left: 0;
  width: 100%;
}
.horizontal-nav a:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0.5px;
  background-color: #000;
  bottom: calc(-1px);
  right: 0;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.vertical-nav {
  list-style-type: none;
  margin-top: 0px;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vertical-nav li {
  display: inline-block;
  min-width: 5em;
  margin: 0 2em;
  padding: 10px 0px;
}
.vertical-nav a {
  text-decoration: none;
  display: block;
  font-family: Arial;
  font-size: 18px;
  position: relative;
  color: #000;
  padding: 0.5em 0;
}

/* CONTENT */

.article-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.gfx-col {
  width: 40%;
  /*min-height: calc(33% - 2 * 110px); */
  height: 22vh;
  padding: 30px;
}

.text-col {
  width: 60%;
  /* min-height: calc(33% - 2 * 110px); */
  height: 22vh;
  padding: 30px;
  font-size: 16px;
  opacity: 0.6;
  overflow: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  -moz-box-shadow: 10px 10px 5px #ccc;
  -webkit-box-shadow: 10px 10px 5px #ccc;
  box-shadow: 10px 10px 5px #ccc;
}

/* FOOTER */
.social-icons {
  padding: 10px 0;
  margin-left: auto;
  color: white;
  text-decoration: none;
}

.social-item {
  margin: 0 10px;
  text-decoration: none;
  color: white;
  font-size: 40px;
}
.copy {
  color: white;
  font-size: 20px;
}
#footer {
  display: flex;
  flex: row;
  justify-content: space-between;
}
